import i18n from '@/assets/i18n'

export default {
  props: {
    lang: {
      type: String,
      default: 'EN'
    }
  },
  data () {
    return {
      content: i18n.translate('en')
    }
  },
  watch: {
    lang: {
      immediate: true,
      handler () {
        this.content = i18n.translate(this.lang?.toLowerCase())
      }
    }
  }
}
